<template>
  <tab :line-width=2 active-color='#fc378c' :index.sync="index" class="jq-test">
      <tab-item class="vux-center" :selected="demo === item" v-for="item in tablist" @click="tabItemChange">{{item}}</tab-item>
  </tab>
 <swiper :index.sync="index" height="10rem" :show-dots="false" class="clearfix jq-te">
      <swiper-item height="23rem">
          <group v-if='!isbusshow' class="jq-mt">
            <popup-picker :title="dateTile" :data="timeList" :value.sync="time"></popup-picker>
           </group>
          
         <group  class="jq-mt" v-if='isbusshow'>
         <flexbox class="jian-bg">
        <flexbox-item class='jia-bg'>
         <popup-picker :title="busTitle" :data="busList" :value.sync="bus"></popup-picker>
        </flexbox-item>
        <flexbox-item class='jia-bg'>
          <popup-picker :title="dateTile" :data="timeList" :value.sync="time"></popup-picker>
        </flexbox-item>
      </flexbox>
    </group>

       <group class="jq-mt">
         <div class="chart" v-ebarline="barLineData"></div>
       </group>
    </swiper-item>

    <swiper-item height="23rem">
         <group v-if='!isbusshow' class="jq-mt">
          <popup-picker :title="dateTile" :data="timeList" :value.sync="timeDeliver" ></popup-picker>
         </group>

      <group  class="jq-mt" v-if='isbusshow'>
         <flexbox class="jian-bg">
        <flexbox-item class='jia-bg'>
         <popup-picker :title="busTitle" :data="busList" :value.sync="busDeliver" ></popup-picker>
        </flexbox-item>
        <flexbox-item class='jia-bg'>
            <popup-picker :title="dateTile" :data="timeList" :value.sync="timeDeliver" ></popup-picker>
        </flexbox-item>
      </flexbox>
    </group>

      <group class="jq-mt">
        <div class="chart" v-ebarline="barLineDataDeliver"></div>
         </group>
     </swiper-item>
    </swiper>
</template>
  <script>
    import {Flexbox, FlexboxItem,Group,Tab, TabItem,Cell,Swiper,SwiperItem,PopupPicker} from 'vux/src/components'
    import { getList } from '../../../untils/api'
    const list = () => ['计划完成率', '及时交付率']
    export default {
    components: {
    Group,
    Tab,
    Flexbox, 
    FlexboxItem,
    Cell,
    PopupPicker,
    TabItem,
    Swiper,
    SwiperItem
    },
    data () {
    return {
    busTitle:'事业部',
    busList: [formatData.busList],
    demo: this.$route.query.demo,
    tablist:list(),
    bus:["FPI"],
    barLineData:{},
    busDeliver:["FPI"],
    barLineDataDeliver:{},
    dateTile:'日期',
    time: formatData.defaultTime,
    timeDeliver: formatData.defaultTime,
    timeList: formatData.dateList,
    index:0,
    isClick:0,
    permissions:localStorage.getItem("permissions"),
    isbusshow:true
    }
    },
    ready(){
      document.title='出货管理';
      if(this.permissions=='1'){

      }else if(this.permissions=='201'){
        this.isbusshow=false;
      }
      else if(this.permissions=='202'){//ADD
       this.isbusshow=false;
        this.bus=['ADD'];
        this.busDeliver=['ADD'];
      }else if(this.permissions=='203'){//SMD,FPI
        this.isbusshow=false;
        this.bus=['SMD'];
        this.busDeliver=['SMD'];
      }else{
          this.isbusshow=false;

      }
      this.getData();

    },
    methods: {
    tabItemChange(){
      if(this.index==0){
        if(this.isClick==0){
        this.getDataDeliver();
        this.isClick++;
      }
      }
    },
    async getData() {
        commit('UPDATE_LOADING', true)
    let id ='';
    if(this.bus=='FPI'){
      id="APP10112";
    }else if(this.bus=='SMD'){
     id="APP10113";
    }else if(this.bus=='ADD'){
     id="APP10114";
    }
    let date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
   
    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barLineData = {};
    this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    },
     async getDataDeliver() {
         commit('UPDATE_LOADING', true)
    let id ='';
    if(this.busDeliver=='FPI'){
      id="APP10115";
    }else if(this.busDeliver=='SMD'){
     id="APP10116";
    }else if(this.busDeliver=='ADD'){
     id="APP10117";
    }
    let date=formatData.dateConversion(this.timeDeliver);
   let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
   
    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barLineDataDeliver = {};
    this.barLineDataDeliver.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    }
    },
     watch: {
    index (newIndex, oldIndex) {
     if(this.index==1)//存仓
    {
       if(this.isClick==0){
        this.getDataDeliver();
        this.isClick++;
      }
    }
    },
    bus (newbus, oldbus) {
        this.getData();

    },
    busDeliver (newbus, oldbus) {
        this.getDataDeliver();

    },
    time (newtime, oldtime) {
      this.getData();
      formatData.setDateTime(newtime);
     },
     timeDeliver(newtime, oldtime) {
        this.getDataDeliver();
          formatData.setDateTime(newtime);
     },
  },
    created: function () {
      if(this.demo==null){
          this.demo="计划完成率";
      }
    this.barLineData = {};
    this.barLineData.dataset = [];
    this.barLineDataDeliver = {};
    this.barLineDataDeliver.dataset = [];
    }
    }
  </script>






